// const crypto = require('crypto');
import sha256 from 'crypto-js/sha256';
import hmacSHA256 from 'crypto-js/hmac-sha256';
import Base64 from 'crypto-js/enc-base64';
import {SECRETKEY, THEME} from "./theme";

export const SignCheck = ()=> {
    /**
     *   .
     * @param searchOrParsedUrlQuery
     * @param {string} secretKey
     * @returns {boolean}
     */
    function verifyLaunchParams(searchOrParsedUrlQuery, secretKey) {
        let sign;
        const queryParams = [];

        searchOrParsedUrlQuery = searchOrParsedUrlQuery.replace(window.location.hash,'');
        /**
         * ,    query-.   
         * ,   ,  "sign".   
         *         
         *  .
         * @param key
         * @param value
         */
        const processQueryParam = (key, value) => {
            if (typeof value === 'string') {
                if (key === 'sign') {
                    sign = value;
                } else if (key.startsWith('vk_')) {
                    queryParams.push({key, value});
                }
            }
        };

        if (typeof searchOrParsedUrlQuery === 'string') {
            //      (  window.location.search),
            //   .
            const formattedSearch = searchOrParsedUrlQuery.startsWith('?')
                ? searchOrParsedUrlQuery.slice(1)
                : searchOrParsedUrlQuery;

            //     query-.
            for (const param of formattedSearch.split('&')) {
                const [key, value] = param.split('=');
                processQueryParam(key, value);
            }
        } else {
            for (const key of Object.keys(searchOrParsedUrlQuery)) {
                const value = searchOrParsedUrlQuery[key];
                processQueryParam(key, value);
            }
        }
        //   ,       ,
        //   ,   "vk_",  
        //  ,      .
        if (!sign || queryParams.length === 0) {
            return false;
        }
        //          .
        const queryString = queryParams
        //     .
            .sort((a, b) => a.key.localeCompare(b.key))
            //      .
            .reduce((acc, {key, value}, idx) => {
                return acc + (idx === 0 ? '' : '&') + `${key}=${encodeURIComponent(value)}`;
            }, '');

        //        .
        // const paramsHash = crypto
        //     .createHmac('sha256', secretKey)
        //     .update(queryString)
        //     .digest()
        //     .toString('base64')
        //     .replace(/\+/g, '-')
        //     .replace(/\//g, '_')
        //     .replace(/=$/, '');
        const paramsHash = Base64.stringify(hmacSHA256(queryString, secretKey))
            .toString('base64')
            .replace(/\+/g, '-')
            .replace(/\//g, '_')
            .replace(/=$/, '');

        // console.log('paramsHash = ',paramsHash);

        return paramsHash === sign;
    }

    const url = window.location.href;
    // const clientSecret = 'fAUg0gYRAlz2vnEtdyZj'; //       
    // const clientSecret = 'vgcTCmjuXJ0xGNJtyNCl'; //       -
    // const clientSecret = 'hCmoa4kyGj76vupsvPYt'; //      
    const clientSecret = SECRETKEY; //      
    console.log("!!!!!!!!",clientSecret)

//    .
    const launchParams = decodeURIComponent(url.slice(url.indexOf('?') + 1));

// ,    .
    const areLaunchParamsValid = verifyLaunchParams(launchParams, clientSecret);

    // console.log(areLaunchParamsValid ? 'ok' : 'fail');

    return areLaunchParamsValid;
}