export const CheckModifyProductAccordance = (modify1,modify2)=>{
    // console.log('     ',modify1,modify2);
    let StatusAddNewProduct = true; //    true -  
    let numberOldProduct = 0;
    let statuSimilar = []; //       

    modify1.forEach((dataOldModify,numOldModify)=>{
        //        
        statuSimilar[numOldModify] = false; //         , false -  , true - 

        modify2.forEach((dataNewModify,numNewModify)=>{
            //         
            if (dataOldModify['idModify'] == dataNewModify['idModify']) {
                // ,      true
                statuSimilar[numOldModify] = true;
                // console.log(' ,   ,  ',dataOldModify,dataNewModify);
            }
        });
    });

    let allStatus = true; //    ,   true - ,   

    if(modify1.length !== modify2.length){
        allStatus = false; // -   ,   false -   
    }

    statuSimilar.forEach((status,n)=>{
        if(status == false){
            allStatus = false;//   ,     ,     
            // console.log(' ,    false');
        }else{
            // console.log(',   ');
        }
    });

    // console.log('  ,  allStatus=',allStatus);

    if(allStatus == false ){ //   ,       false,    
        StatusAddNewProduct = false; //      
        numberOldProduct = 0; // ,    ,       - 
    }

    return StatusAddNewProduct;
}