import React from 'react';

export const CheckWorkTimeAffiliateNew = (StartHh,StartMh,StopHh,StopMh)=>{


    const getNumberPlusNull = (str)=>{
        let returns = 0;

        if(str*1 < 9){
            returns = '0'+str;
        }else{
            returns = str;
        }
        return returns;
    };

    //console.log(Start,Stop);

    const getParseDateStr = (HourH,HourM)=>{
        const d = new Date();
        // let Now = new Date(d.getFullYear(),(d.getMonth()+1),d.getDate(),HourH,HourM,0); //      +1  ,    ( )     ,        1  
        let Now = new Date(d.getFullYear(),(d.getMonth()),d.getDate(),HourH,HourM,0);
       //console.log(Now);
        return Now.getTime();
    };


    const getParseDateStrPlus = (dates,HourH,HourM)=>{
        const rd = dates;
        // let Now = new Date(rd.getFullYear(),(rd.getMonth()+1),rd.getDate(),HourH,HourM,0); //      +1  ,    ( )     ,        1  
        let Now = new Date(rd.getFullYear(),(rd.getMonth()),rd.getDate(),HourH,HourM,0);
        return Now.getTime();
    };

    const getdayDate = (a = false)=>{
        let dd = new Date();
        //console.log('', d.getDate());
        //       1 
        if(a === false) {
            dd.setDate(dd.getDate() - 1);
        }else{
            dd.setDate(dd.getDate() + 1);
        }
        //console.log('   '+d.getDate());
        return dd;
    };

    const getTimeWork = (StartH,StartM,StopH,StopM)=>{
        //   
        //  
        // 
        let start_work = StartH;
        // 
        let end_work = StopH;
        // 
        let currentTime = new Date();

        //   ,      12:00    
        let currentDateTime = getParseDateStr(currentTime.getHours(),currentTime.getMinutes());

        //console.log('     ',currentDateTime);
       // console.log('StartH,StartM,StopH,StopM ',StartH,StartM,StopH,StopM);

        //       
        let previousDayEnd;

        //       
        let startDateTime;

        //       
        let endDateTime;

        let stopDateTime;

        startDateTime = getParseDateStr(StartH,StartM);
        stopDateTime = getParseDateStr(StopH,StopM);

        if (startDateTime <= stopDateTime){

            let yesterday = getdayDate(false);

            endDateTime = stopDateTime;
            previousDayEnd = getParseDateStrPlus(yesterday,StopH,StopM);

           //console.log(' ');
        }
        else{

            let tomorrow = getdayDate(true);

            endDateTime = getParseDateStrPlus(tomorrow,StopH,StopM);
            previousDayEnd = getParseDateStr(StopH,StopM);
           //console.log(' ');
        }

       //console.log('  ',currentTime, '   ',currentTime.getHours(),currentTime.getMinutes());
       //console.log('currentDateTime: '+currentDateTime,'previousDayEnd: '+previousDayEnd,'startDateTime: '+startDateTime,'endDateTime: '+endDateTime);

        //  
        // echo "   : " .  date("Y-m-d H:i:s", previousDayEnd). "\n";
        // echo " : " . date("Y-m-d H:i:s", startDateTime) . "\n";
        // echo "  : " .  date("Y-m-d H:i:s", endDateTime). "\n";

        if (currentDateTime >= startDateTime && currentDateTime <= endDateTime) {
            // echo "   : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('   1');
            return true;
        }
        else if(currentDateTime < startDateTime && currentDateTime < previousDayEnd ){
            // echo "   : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('   2');
            return true;
        }
        else {
            // echo "     : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('  ');
            return false;
        }

    };


    return getTimeWork(StartHh,StartMh,StopHh,StopMh);

};