import React, {useCallback} from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
import {addProductBasket} from "../../store/actions/post";
import {CheckModifyProductAccordance} from "../CheckModifyFunction/CheckModifyProductAccordance";
// import {GetAllPriceBasket} from "../Basket/GetAllPriceBasket";

export const ButtonInCartullWidth = ({ Product, titleButton = ' ', SizeButton = 'normal', priceTextButton = null, typeProductModify = false, goBack = ()=>{}}) => {

    const dispatch = useDispatch();
    const basketProduct = useSelector(state => state.post.basket);
    let inCart = false;
    let countInCart = 0;

    const modificationActive = useSelector(state => state.post.modifyTmp);
    const ActiveButtonSetProduct = useSelector(state => state.post.activeButtonSetProduct);


    const reloadBasketData = (idProduct,TypeProduct)=>{
        basketProduct.forEach((product)=>{
            //console.log(product.id, idProduct);
            if(product.id === idProduct){
                product.type.forEach((typeId,numb)=>{
                    if(typeId.type_id === TypeProduct){
                        //console.log(' ,    ');
                        inCart = true;
                        countInCart = typeId.count;
                    }else{
                        //console.log(typeId, TypeProduct);
                        //console.log('     ');
                    }
                });
            }else{
                //console.log('   ');
            }
        });
    }

    let PrArray =[];


    const addTypeProduct = (i,TypeProduct,idCategory,modificationActiveSet = [],modifyProduct = false)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type.push(
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: 1,
                        modifyList:modificationActiveSet,
                        modifyProduct:modifyProduct
                    }
                );
            }
        });
        return ProductsType;
    };


    const reloadTypeProduct = (i,TypeProduct,idCategory,num,count,modificationActiveSet = [],modifyProduct = false)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type[num] =
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: count,
                        modifyList:modificationActiveSet,
                        modifyProduct:modifyProduct
                    }
                ;
            }
        });
        //console.log(num,ProductsType);
        return ProductsType;
    };

    const RemoveFullTypeProduct = (i,TypeProduct,num,count)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                if(ProductsType[i].type.length<=1){
                    ProductsType[i].type.splice(num, 1);
                    //console.log(' ');
                }else{
                    ProductsType[i].type.forEach((PrType,nn)=>{
                        if(PrType.type_id === TypeProduct){
                            ProductsType[i].type.splice(nn, 1);
                            //console.log(ProductsType[i].type);
                        }else{}
                    });
                }
            }
        });
        //console.log(ProductsType[i].type);
        return ProductsType;
    };

    const setOneProduct = (idProduct,TypeProduct,idCategory,modificationActiveSet = [],modifyProduct = false)=>{
        return {
            id:idProduct,
            "type":[
                {
                    type_id:TypeProduct,
                    categoryId:idCategory,
                    count: 1,
                    modifyList:modificationActiveSet,
                    modifyProduct:modifyProduct
                }]
        };
    };


    const checkCartProduct = (idProduct,TypeProduct,idCategory, typeProductModify = false)=>{
        // console.log(" ");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;
        let ModifyCheckProduct = false;

        if(typeProductModify === true){
            // console.log('         ,   ', modificationActive);

            //   

            let StatusAddNewProduct = true; // ,            .
            let countAllProductInCart = 0; //-   ,  . -       .
            let numberOldProduct = null; //     ,      ,   null,    


            if(basketProduct.length){
                //   ,  
                basketProduct.forEach((product,i)=>{
                    //        
                    if(product.id === idProduct){
                        // id    ,    
                        ProductInBasket = true; //  ,     (id)
                        product.type.forEach((typeId,numb)=>{
                            //   ,      id
                            if(typeId.type_id === TypeProduct){
                                //              

                                //  -        ,         ,
                                //       ,  - ,   
                                if(modificationActive.length === typeId?.modifyList?.length){
                                    ModifyCheckProduct = CheckModifyProductAccordance(typeId?.modifyList,modificationActive); //true    , false    
                                }else{}

                                if(ModifyCheckProduct === true){
                                    ProductTypeInBasket = true; // ,    , id  
                                    //console.log(' ,    ');
                                    PrArray = [...basketProduct];
                                    reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count+1,modificationActive,true) // -  1,   
                                }

                                //  -       ,   ,      - (count)
                                //  

                                //   -          , 
                            }
                        });


                        if(ProductInBasket & !ProductTypeInBasket){
                            // ,      ,    id
                            //console.log('     ');
                            PrArray = addTypeProduct(i,TypeProduct,idCategory,modificationActive,true);
                            //console.log('    ');
                        }
                    }
                });

                if(!ProductInBasket){
                    //  id       ,    
                    //   ,     
                    PrArray = [...basketProduct];
                    PrArray.push(setOneProduct(idProduct,TypeProduct,idCategory,modificationActive,true));
                }
            }else{
                // ,   
                PrArray = [setOneProduct(idProduct,TypeProduct,idCategory,modificationActive,true)];
            }

        }else{
            //  


            if(basketProduct.length){
                basketProduct.forEach((product,i)=>{
                    if(product.id === idProduct){
                        ProductInBasket = true;
                        product.type.forEach((typeId,numb)=>{
                            if(typeId.type_id === TypeProduct){
                                ProductTypeInBasket = true;
                                //console.log(' ,    ');
                                PrArray = [...basketProduct];
                                reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count+1)
                            }
                        });
                        if(ProductInBasket & !ProductTypeInBasket){
                            //console.log('     ');
                            PrArray = addTypeProduct(i,TypeProduct,idCategory);
                            //console.log('    ');
                        }
                    }
                });

                if(!ProductInBasket){
                    //   
                    PrArray = [...basketProduct];
                    PrArray.push(setOneProduct(idProduct,TypeProduct,idCategory));
                }
            }else{
                // ,   
                PrArray = [setOneProduct(idProduct,TypeProduct,idCategory)];
            }

        }

    };


    const RemoveFullProduct = (idProduct)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(PR.id === idProduct && PR.type.length<=1){
                //console.log('   '+idProduct, PR);
                ProductsType.splice(n, 1);
            }
        });
        PrArray = [...ProductsType];
    };

    const checkCartProductRemove = (idProduct,TypeProduct,idCategory)=>{
        // console.log(" ");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;

        let RemoveAllProduct = true;

        if(basketProduct.length){
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    ProductInBasket = true;
                    if(product.type.length>1){
                        RemoveAllProduct = false;
                    }
                    product.type.forEach((typeId,numb)=>{
                        if(typeId.type_id === TypeProduct){
                            ProductTypeInBasket = true;
                            //console.log(' ,    ');
                            PrArray = [...basketProduct];
                            if(typeId.count<=1){
                                RemoveFullTypeProduct(i,TypeProduct,idCategory,numb);
                            }else{
                                if(typeId.count>1){
                                    RemoveAllProduct = false;
                                }
                                reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count-1);
                            }
                        }else{
                            if(typeId.count>0){
                                //console.log('   ',typeId);
                                //reloadTypeProduct(i,typeId.type_id,idCategory,numb,typeId.count-1);
                                RemoveAllProduct = false;
                            }
                        }
                    });
                }
                if(ProductInBasket & !ProductTypeInBasket){
                    //console.log('     ,  ');
                    //RemoveFullProduct(idProduct);
                }

            });

            if(RemoveAllProduct){
                RemoveFullProduct(idProduct)
            }

        }else{
            // ,   
            PrArray = [];
        }
    };

    // const addProductBasketHandler = useCallback((id,type_id) => {
    //     checkCartProduct(id,type_id)
    //     dispatch(addProductBasket(PrArray));
    // }, [dispatch,PrArray]);


    const addProductBasketHandler = async (id,type_id,idCategory, typePrModify = false) => {
        await checkCartProduct(id,type_id,idCategory,typePrModify);
        await dispatch(addProductBasket(PrArray));
        goBack();
    };


    const removeProductBasketHandler = (id,type_id,idCategory) => {
        checkCartProductRemove(id,type_id,idCategory);
        dispatch(addProductBasket(PrArray));
    };


    const returnButtonIncart = (typeProductModify = false)=>{
        reloadBasketData(Product.id,Product.type_product);
        if(ActiveButtonSetProduct.status === true){
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category,typeProductModify)}>
                    <View style={
                        SizeButton == 'normal' ? styles.ButtonBlock : styles.ButtonBlockMini}>
                        <View>
                            <Text style={SizeButton == 'normal' ?  styles.buttonText : styles.buttonTextMini }>
                                {titleButton}
                            </Text>
                        </View>
                        <View style={{display:"flex",justifyContent:"center",alignItems:"center",paddingLeft:5}}>
                                {priceTextButton}
                        </View>
                    </View>
                </TouchableOpacity>
            )
        }else{
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={() => {}}>
                    <View style={
                        SizeButton == 'normal' ? styles.ButtonBlockNoActive : styles.ButtonBlockMini}>
                        <View>
                            <Text style={SizeButton == 'normal' ?  styles.buttonTextNoActive : styles.buttonTextMini }>
                                {ActiveButtonSetProduct.text}
                            </Text>
                        </View>
                    </View>
                </TouchableOpacity>
            )
        }
    };

    return returnButtonIncart(typeProductModify)
};

const styles = StyleSheet.create({
    BlockButtonCount:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        // width: 100
    },
    CountProductInCart:{
        color: "#252622",
        paddingHorizontal: 10
    },
    CountProductInCartText:{
        color: "#252622",
    },
    ButtonBlock: {
        paddingHorizontal:13,
        paddingVertical:11,
        paddingBottom:10,
        backgroundColor:THEME.MAIN_COLOR,
        width:"98%",
        marginLeft:"1%",
        borderRadius:10,textAlign: "center",
        display:"flex",
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    ButtonBlockNoActive: {
        paddingHorizontal:13,
        paddingVertical:11,
        paddingBottom:10,
        backgroundColor:THEME.BORDER_COLOR_DEFAULT,
        width:"98%",
        marginLeft:"1%",
        borderRadius:10,textAlign: "center",
        display:"flex",
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    ButtonBlockMini: {
        paddingHorizontal:10,
        paddingVertical:2,
        paddingBottom:4,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:7
    },
    buttonText:{
        fontFamily:"open-bold",
        fontSize:14,
        color:THEME.TEXT_COLOR,
        textAlign: "center"
    },

    buttonTextNoActive:{
        fontFamily:"open-bold",
        fontSize:14,
        color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT,
        textAlign: "center"
    },
    buttonTextMini:{
        fontFamily:"open-bold",
        fontSize:10,
        color:THEME.TEXT_COLOR
    }
});
